
import './App.css';
import { Link, useNavigate } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import tab from "./tab.json";
import tabpanel from "./tabpanel.json";
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
function App() {
  const [show, setShow] = useState(false);
  const [showModelData, setShowModelData] = useState(null);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])


  return (
    <>
      <div className='icon-tab'>
        <Tabs>
          <TabList>
            {tab.map((tab) => {
              return (
                <Tab>{tab.name}</Tab>
              )
            })}
          </TabList>

          {tabpanel.map((tabpanel) => {
            return (
              <TabPanel>
                {tabpanel.data.map(ds => {
                  return (
                    <div className='icon-box' onClick={() => {
                      setShowModelData(ds)
                      handleShow()
                      
                    }}>
                      <img src={ds.images} />
                      <h2>{ds.name}</h2>
                    </div>
                  )
                })}

                <Modal show={show} onHide={handleClose}
                  size="lg"
                  aria-labelledby="contained-modal-title-vcenter"
                  centered
                  dialogClassName="modal-50w"
                >
                  <Modal.Body>
                    <h2>{showModelData?.categories} </h2>
                  </Modal.Body>
                </Modal>

              </TabPanel>
            )
          })}
        </Tabs>
      </div>
    </>
  );
}

export default App;
